import React from 'react';
import { ArrangementOrder } from '../types';

type TUseOrder = {
	order: ArrangementOrder;
	orderBy: string;
	handleRequestSort: (e: React.SyntheticEvent, p: string) => void;
};

const useOrder = (defaultProp: string): TUseOrder => {
	const [order, setOrder] = React.useState<ArrangementOrder>('desc');
	const [orderBy, setOrderBy] = React.useState<string>(defaultProp);

	const handleRequestSort = (event: React.SyntheticEvent, property: string) => {
		const isAsc = orderBy === property && order === 'asc';
		setOrder(isAsc ? 'desc' : 'asc');
		setOrderBy(property);
	};

	return { order, orderBy, handleRequestSort };
};

export default useOrder;
